.Episode {
    background-color: rgb(3, 8, 39);
}

.episode-main {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
    justify-content: center;
    align-items: center;
}

.episode-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80%;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 100px;
}

.episode-image img {
    width: 500px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
}

.episode-details {
    width: 75%;
    height: 280px;
    padding-left: 20px;
    background-color: rgb(255, 255, 255);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}