.App {
  text-align: center;
  background-color: rgb(3, 8, 39);
  overflow: scroll;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
}

.tab-selection {
  display: flex;
  flex-direction: row;
}

.tabSelected {
    text-decoration: underline;
    -webkit-text-decoration-color: #3E4E56;
    text-decoration-color: whitesmoke; 
    font-weight: 450;
}

.tab {
  cursor: pointer;
  padding-left: 7.5px;
  padding-right: 7.5px;
  font-size: calc(9px + 2vmin);
  color: white;
}

.tab-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  height: 100%;
}

.left {
  display: flex;
  justify-content: flex-start;
}

.right {
  display: flex;
  justify-content: flex-end;
}