.ImageCell {
    display: flex;
    flex-direction: column;
    background-color: rgba(134, 134, 134, 0.63);
    border-radius: 5px;
    margin-bottom: 15px;
}

.image-container img {
    width: 100%;
    height: auto;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.cell-text {
    color: white;
    font-size: 15px;
    font-weight: 450;
}

.content-row {
    display: flex;
    flex-direction: row;
}

.content-column {
    display: flex;
    flex-direction: column;
    width: 50%;
}