.Character {
    background-color: rgb(3, 8, 39);
}

.character-main {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
    justify-content: center;
    align-items: center;
}

.character-content {
    display: flex;
    flex-direction: row;
    width: 80%;
    margin-top: 15px;
    margin-bottom: 20px;
}

.character-image img {
    width: 400px;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
}

.character-details {
    width: 75%;
    padding-left: 20px;
    background-color: rgb(255, 255, 255);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}